export interface ChartData {
  x: any;
  y: any;
}


export function mapToChartData(obj: any, xProperty: string, yProperty: string): ChartData {
  return {x: obj[xProperty], y: obj[yProperty] };
}
export function prepareLineDataset(title: string, color: string, data: Array<any>) {
  return {
    label: title,
    data: data,
    backgroundColor: color,
    borderColor: color,
    pointRadius: 1,
    lineTension: 0,
    borderWidth: 2,
    fill: false,
  };
}

