import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { map } from 'rxjs/operators';
import { AppUserViewModel } from '../../../generated-api/models';
import { UsersService } from '../../../generated-api/services';
import { emptyStringIfNull } from '../../../utils/utils';
@Component({
  selector: 'ngx-account-settings',
  templateUrl: './account-settings.component.html',
  styleUrls: ['./account-settings.component.scss'],
})
export class AccountSettingsComponent implements OnInit {

  public errors: string[] = [];
  public showMessages: any = {
    error: true,
    success: true,
  };
  public saveSubmitted = false;

  public user: AppUserViewModel;

  @ViewChild('form')
  private form: NgForm;
  private successfulResponseReceived = false;

  constructor(
    private readonly usersService: UsersService) {
  }

  ngOnInit() {
    this.loadAccount();
  }

  private loadAccount() {
    this.usersService.GetMyAccount().pipe(map(d => d.content))
      .subscribe(user => {
        this.user = user;
      });
  }

  public shouldShowSuccess(): boolean {
    return this.showMessages.success && !this.saveSubmitted && this.successfulResponseReceived;
  }

  public shouldShowErrors(): boolean {
    return this.showMessages.error && this.errors && this.errors.length > 0 && !this.saveSubmitted;
  }

  public save(): void {
    this.errors = [];
    this.saveSubmitted = true;
    this.successfulResponseReceived = false;

    this.usersService.Put(this.user).pipe(map(d => d.content))
      .subscribe(() => {
        this.saveSubmitted = false;
        this.successfulResponseReceived = true;
      });
  }

  public shouldBeSaveBtnDisabled(): boolean {
    return this.saveSubmitted ||
      this.user == null ||
      // !this.form.valid ||
      emptyStringIfNull(this.user.password) !== emptyStringIfNull(this.user.confirmPassword);
  }
}
