import { Component, OnInit, ViewChild } from '@angular/core';
import { SmartTableComponent } from '../../../@theme/components/smart-table/smart-table.component';
import { ApsAlertViewModel, FieldDefinitionViewModel } from '../../../generated-api/models';
import { EntityFieldsDefinitionsService, ApsAlertsService } from '../../../generated-api/services';
import { extractContent } from '../../../utils/mappers';
import { ServiceDataSource } from '../../../utils/service-data-source';
@Component({
  selector: 'ngx-alerts',
  templateUrl: './alerts.component.html',
  styleUrls: ['./alerts.component.scss']
})
export class AlertsComponent implements OnInit {

  dataSource: ServiceDataSource<ApsAlertViewModel>;
  fieldsModel: FieldDefinitionViewModel[];
  @ViewChild('table') table: SmartTableComponent<ApsAlertViewModel>;


  constructor(private readonly remoteService: ApsAlertsService,
    private readonly fieldDefinitionService: EntityFieldsDefinitionsService) {
  }

  ngOnInit() {
    this.fieldDefinitionService.ModelFieldsDefinitions('aps-alert')
      .pipe(extractContent())
      .subscribe(fields => {
        this.fieldsModel = fields;
        this.table.refresh();
        this.initDataSource();
      });

  }


  private initDataSource(): void {
    const service = this.remoteService;
    this.dataSource = new ServiceDataSource(
      service.Get().pipe(extractContent()),
      v => service.Post(v).pipe(extractContent()),
      v => service.Put(v).pipe(extractContent()),
      v => service.Delete_1(v.id).pipe(extractContent()),
    );
  }

}
