import { Component, OnInit, ViewChild } from '@angular/core';
import { SmartTableComponent } from '../../../@theme/components/smart-table/smart-table.component';
import { AppUserViewModel, FieldDefinitionViewModel } from '../../../generated-api/models';
import { EntityFieldsDefinitionsService, UsersService } from '../../../generated-api/services';
import { extractContent } from '../../../utils/mappers';
import { ServiceDataSource } from '../../../utils/service-data-source';
@Component({
  selector: 'ngx-users-settings',
  templateUrl: './users-settings.component.html',
})
export class UsersSettingsComponent implements OnInit {

  dataSource: ServiceDataSource<AppUserViewModel>;
  fieldsModel: FieldDefinitionViewModel[];
  @ViewChild('table') table: SmartTableComponent<AppUserViewModel>;


  constructor(private readonly usersService: UsersService,
    private readonly fieldDefinitionService: EntityFieldsDefinitionsService) {
  }

  ngOnInit() {
    this.fieldDefinitionService.ModelFieldsDefinitions('user')
      .pipe(extractContent())
      .subscribe(fields => {
        this.fieldsModel = fields;
        this.table.refresh();
        this.initDataSource();
      });

  }


  private initDataSource(): void {
    const service = this.usersService;
    this.dataSource = new ServiceDataSource(
      service.Get().pipe(extractContent()),
      v => service.Post(v).pipe(extractContent()),
      v => service.Put(v).pipe(extractContent()),
      v => service.Delete_1(v.id).pipe(extractContent()),
    );
  }




}
