import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {ApsAlertsService, ApsItemsService, EntityFieldsDefinitionsService} from '../../../generated-api/services';
import {flatMap, map, groupBy, mergeMap, toArray, mergeAll, reduce} from 'rxjs/operators';
import {ApsAlertViewModel, ApsItemViewModel, FieldDefinitionViewModel,DetailedApsItemViewModel} from '../../../generated-api/models';
import {ServiceDataSource} from '../../../utils/service-data-source';
import {extractContent} from '../../../utils/mappers';
import {from, of} from 'rxjs';
import {DialogService} from '../../../@theme/components/dialog/dialog.service';
import {byPropertyAsc} from '../../../utils/sorters';
import {defaultTimeChartOptions, prepareLineDataset} from '../../../utils/charts-utilities';
import * as moment from 'moment'
import {FormModel} from "../../../@theme/components/dynamic-form/dynamic-form.component";
@Component({
  selector: 'ngx-item-details',
  templateUrl: './item-details.component.html',
  styleUrls: ['./item-details.component.scss']
})
export class ItemDetailsComponent implements OnInit {
  item: DetailedApsItemViewModel;
  charOptions: any;
  batteryData: {};
  temperatureData: {};
  powerData: {};
  energyData: {};

  itemsDataSource: ServiceDataSource<ApsItemViewModel>;
  alertsDataSource: ServiceDataSource<ApsAlertViewModel>;
  itemsFieldsModel: FieldDefinitionViewModel[];
  alertsFieldsModel: FieldDefinitionViewModel[];

  settings = {
    actions: {
      add: false,
      edit: false,
      delete: false,
      custom: [{name: 'edit', title: '<i class="nb-alert"></i>'}],
    },
    extraActions: {
      add: false,
    },
    columns: {},
  };




  constructor(
    private readonly fieldDefinitionService: EntityFieldsDefinitionsService,
    private readonly alertsService: ApsAlertsService,
    private readonly itemsService: ApsItemsService,
    private readonly route: ActivatedRoute,
    private readonly dialogService: DialogService,
  ) {
  }

  ngOnInit() {
    this.route.paramMap.pipe(
      map(params => params.get('imei')),
      flatMap(imei => this.itemsService.GetAllByImei(imei)))
      .pipe(extractContent())
      .subscribe(item => {
        this.item = item;
        this.initCharts();
        this.initDataSources();
      });


    this.fieldDefinitionService.ModelFieldsDefinitions('aps-alert')
      .pipe(extractContent())
      .subscribe(fields => {
        this.alertsFieldsModel = fields;
        this.initDataSources();
      });
    this.fieldDefinitionService.ModelFieldsDefinitions('aps-item')
      .pipe(extractContent())
      .subscribe(fields => {
        this.itemsFieldsModel = fields;
        this.initDataSources();
      });
  }

  onCustomAction(event) {
    this.dialogService.showDynamicEditorDialog({
      fieldsDefinitions: this.itemsFieldsModel, object: event.data, dialogName: 'View details', editable: false
    });
  }

  private initCharts() {
    this.charOptions = defaultTimeChartOptions;
    const items = this.item.rawData.map(item => ({x: item.usageDate, y: item.batteryDisChargingCurrent02MinAverage}));
    this.batteryData = {
      datasets: [
        prepareLineDataset('Battery discharging current', '#00FFFF', items),
        prepareLineDataset('Battery charging current', '#ff6384',
          this.item.rawData.map(item => ({x: item.usageDate, y: item.batteryChargingCurrent02MinAverage}))),
        prepareLineDataset('Battery SOC', '#0000ff',
          this.item.rawData.map(item => ({x: item.usageDate, y: item.batterySoH}))),
        prepareLineDataset('Grid voltage', '#FFA500',
          this.item.rawData.map(item => ({x: item.usageDate, y: item.gridVoltage12MinHigh}))),
      ]

    };

    this.powerData = {

      datasets: [
        prepareLineDataset('PV power input', '#00FFFF',
          this.item.rawData
            .map(item => ({x: item.usageDate, y: item.pvPower02MinAverage}))),
        prepareLineDataset('Inverter output power', '#FFA500',
          this.item.rawData.map(item => ({x: item.usageDate, y: item.inverterOutputVoltage}))),

      ]

    };
    this.energyData = {
      datasets: [
        {
          label: 'Energy output from inverter',
          data: this.item.groupedEnergyFromInverterByDay,
          backgroundColor: '#00FFFF'
        },
        {
          label: 'Energy from PV',
          data: this.item.groupedEnergyFromPvByDay,
          backgroundColor: '#ff6384'
        },
        {
          label: 'Energy from battery',
          data: this.item.groupedEnergyFromBatteryByDay,
          backgroundColor: '#0000ff'
        },
      ]

    };
    this.temperatureData = {
      datasets: [
        prepareLineDataset('Inverter temperature', '#FFA500',
          this.item.rawData.map(item => ({x: item.usageDate, y: item.inverterTemp}))),
        prepareLineDataset('Battery temperature', '#0000ff',
          this.item.rawData.map(item => ({x: item.usageDate, y: item.batteryTemp}))),
      ]
    };

  }

  private initDataSources() {
    if (this.itemsDataSource == null && this.item != null) {
      this.itemsDataSource = new ServiceDataSource(
        of(this.item.rawData)
      );
      this.alertsDataSource = new ServiceDataSource(
        of([])
      );
    }
  }
}
