import { Component, OnInit, ViewChild} from '@angular/core';
import { SmartTableComponent } from '../../../@theme/components/smart-table/smart-table.component';
import {ApsItemListViewModel, ApsItemViewModel, FieldDefinitionViewModel} from '../../../generated-api/models';
import { EntityFieldsDefinitionsService, ApsItemsService } from '../../../generated-api/services';
import { extractContent } from '../../../utils/mappers';
import { ServiceDataSource } from '../../../utils/service-data-source';
import {Router} from '@angular/router';
@Component({
  selector: 'ngx-items',
  templateUrl: './items.component.html',
  styleUrls: ['./items.component.scss']
})
export class ItemsComponent implements OnInit {

  dataSource: ServiceDataSource<ApsItemListViewModel>;
  fieldsModel: FieldDefinitionViewModel[];
  @ViewChild('table') table: SmartTableComponent<ApsItemListViewModel>;

  settings = {
    actions: {
      add: false,
      edit: false,
      delete: false,
      custom: [{ name: 'edit', title: '<i class="nb-edit"></i>' }],
    },
    extraActions: {
      add: false,
    },
  };

  constructor(
    private readonly router: Router,
    private readonly itemsService: ApsItemsService,
    private readonly fieldDefinitionService: EntityFieldsDefinitionsService) {
  }

  ngOnInit() {
    this.fieldDefinitionService.ModelFieldsDefinitions('aps-item-list')
      .pipe(extractContent())
      .subscribe(fields => {
        this.fieldsModel = fields;
        this.table.refresh();
        this.initDataSource();
      });

  }
  onCustomAction(event) {
    this.router.navigate(['pages/aps/item-details/' + event.data.imei]);
  }

  private initDataSource(): void {
    const service = this.itemsService;
    this.dataSource = new ServiceDataSource(
      service.GetList().pipe(extractContent()),
    );
  }

}
